	const innerAudioContext = uni.createInnerAudioContext()
	innerAudioContext.autoplay = true
	innerAudioContext.loop = true
	innerAudioContext.src = 'https://music.163.com/song/media/outer/url?id=28287132.mp3'
	
	import cuHeader from '@/components/cu-header.vue';
	import cuVideo from '@/components/cu-video.vue';
    export default {
		components: {
			cuHeader,
			cuVideo
		},
        data() {
            return {
				modalName: null,
				directionStr: '水平',
				horizontal: 'right',
				vertical: 'bottom',
				direction: 'vertical',
				pattern: {
					color: '#7A7E83',
					backgroundColor: '#fff',
					selectedColor: '#690000',
					buttonColor: '#690000'
				},
				tabList:["新冠专项","食品安全","医学健康","生活窍门","自然环境","宠物花草","科学技术","神秘现象","传说轶事","其他分类"],
                banner: {},
                listData: [],
                last_id: "",
				reload: false,
				//搜索框相关
				defaultKeyword: "",
				keyword: "",
				oldKeywordList: [],
				hotKeywordList: [],
				keywordList: [],
				forbid: '',
				isShowKeywordList: false,
				category:"新冠专项",
				scrollLeft:5
            }
		},
		onBackPress() {
			if (this.$refs.fab.isShow) {
				this.$refs.fab.close()
				return true
			}
			return false
		},
        onLoad() {
			//自动获取这两个
            this.getList();
            this.init();
		},
		//下拉更新
        onPullDownRefresh() {
            this.reload = true;
            this.last_id = "";
            this.getBanner();
            this.getList();
        },
        onReachBottom() {
            this.getList();
        },
        methods: {
			// Drawer弹出
			showDrawer(){
				uni.showToast({
					title: '您打开了一个彩蛋',
					icon: 'none'
				})
				if( this.modalName == null ){
					this.modalName = 'viewModal';
				}else{
					this.modalName = null;
				}
				console.log(this.modalName)
			},
			showModal(e) {
				this.modalName = e.currentTarget.dataset.target
			},
			hideModal(e) {
				this.modalName = null
			},
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			},
			//流言列表的内容
            getList() {
				uni.showLoading({
					title: '加载中'
				});
                var data = {
					_sort:'date:DESC',//按照时间顺序排序
					_limit:10, //需要的字段名
					category:this.category
                };
                if (this.last_id) { //说明已有数据，目前处于上拉加载
                    data._start = this.last_id;
                }
                uni.request({
					url: 'http://120.79.197.140:1337/rumors',
					method: 'GET',
                    data: data,
                    success: (data) => {
						uni.hideLoading();
                        if (data.statusCode == 200) {
							let list = data.data;
							if(list.length >0){
                                this.listData = this.reload ? list : this.listData.concat(list);
                                this.last_id = list[list.length - 1].id;
                                this.reload = false;
                            } else {
									// this.$refs.popupBottom.open();
                            }
                        }
                    },
                    fail: (data, code) => {
                        console.log('fail' + JSON.stringify(data));
                    }
                })
			},			
			//进入详情页面
            goDetail: function(e) {
                uni.navigateTo({
                    url: "../detail/detail?id=" + e.id
                })
            },	
            //清除输入
            exit:function(e){
                this.$refs.popupBottom.close();

            }
        },
    }
